.scr-navi {
  .swiper-wrapper {
    .swiper-slide {
      a {
        font-size: 4vw;
      }
    }
  }
}
.cs-indmnt {
  padding: 0 4.8vw 16.5vw;
  h3 {
    margin-bottom: 4vw;
    font-size: 3.73vw;
    font-weight: bold;
  }
  .info {
    font-size: 3.73vw;
    color: #aaa;
    line-height: 5.87vw;
  }
  ul li {
    letter-spacing: -1.2px;
  }
}
.cs-indmnt.pg03 {
  padding: 0;
  .box-top {
    padding: 0 4.8vw 8.67vw;
    border-bottom: 2px solid #f7f7f7;
  }
  .box-btm {
    padding: 8.67vw 4.8vw 0;
  }
}
@media (min-width: 750px ) {
  .scr-navi {
    width: 678px;
    border-bottom: 6px #000 solid;
    .swiper-wrapper {
      .swiper-slide a {
        height: 100px;
        line-height: 100px;
        font-size: 30px;
      }
    }
    .swiper-wrapper + .icn-navi {
      height: 100px;
      background-position: right center;
    }
  }
  .cs-indmnt {
    padding: 0 36px 124px;
    h3 {
      margin-bottom: 30px;
      font-size: 28px;
    }
    .info {
      font-size: 28px;
      color: #aaa;
      line-height: 44px;
    }
  }
  .cs-indmnt.pg03 {
    .box-top {
      padding: 36px 65px;
    }
    .box-btm {
      padding: 65px 36px 0;
    }
  }
}
